.app {
  max-width: 500px;
  margin: 40px auto;
  font-family: Arial, sans-serif;
  text-align: center;
  transition: background-color 0.3s, color 0.3s;
}

.dark {
  background-color: #111;
  color: white;
}

h2 {
  margin-bottom: 10px;
}

.theme-toggle {
  margin-bottom: 10px;
  padding: 6px 12px;
  background-color: #555;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.chat-box {
  height: 400px;
  overflow-y: auto;
  border: 1px solid #ccc;
  padding: 10px;
  background-color: #f9f9f9;
}

.dark .chat-box {
  background-color: #222;
  border-color: #555;
}

.message {
  display: flex;
  align-items: flex-start;
  margin: 10px 0;
  gap: 10px;
  text-align: left;
}

.you {
  background-color: #e2e2ff;
  padding: 8px;
  border-radius: 8px;
}

.bot {
  background-color: #fff3cd;
  padding: 8px;
  border-radius: 8px;
}

.dark .you {
  background-color: #3b3b5f;
}

.dark .bot {
  background-color: #665c3d;
  color: #fff;
}

.avatar {
  font-size: 24px;
}

.input-row {
  display: flex;
  margin-top: 10px;
}

input[type="text"] {
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

button {
  padding: 10px 15px;
  font-size: 16px;
  background-color: #4CAF50;
  border: none;
  color: white;
  cursor: pointer;
  margin-left: 5px;
}

.time {
  font-size: 10px;
  color: #555;
  text-align: right;
}
